body{
  position: relative;
  margin: 0;
  padding: 0;
  background: radial-gradient(white,rgb(255, 255, 146));
  background-size: cover;
  background-repeat: no-repeat;
  }


.product-wrap{
    margin: 50px;
    float: left;
    box-shadow: 1px 1px 20px black;
    padding: 10px;
    border-radius: 10px;
}

.img-content{
    width: 300px;
    height: 320px;
}

.img-content img{
    width: 100%;
    height: 100%;
}

.text-center{
    text-align: center;
}

.button-buy{
    margin: 0 4px;
    outline: none;
    border:none;
    padding: 10px 25px;
    border-radius: 10px;
    background: transparent;
    border:2px solid rgb(13, 117, 214);
    color: rgb(13, 117, 214);
    cursor: pointer;
    transition: .5s;
    box-sizing: border-box;
}

.button-buy:hover{
    transition: .5s;
    background: rgb(13, 117, 214);
    color: white;
}
